import back from '@/components/back'
import noData from '@/components/noData'
export default {
    components:{
        back,
		noData
    },
    data(){
        return {
			page: 1,
			list:[],
			haveMore:false,
			pageShow:false,
            search: this.$route.query.searchName?this.$route.query.searchName:"",
            user: this.$store.state.userInfo.Data,
			ClassId:this.$route.query.ClassId?this.$route.query.ClassId:0,
        }
    },
    created(){
        this.pull()
    },
	mounted(){
		this.getMore()
	},
    methods:{
		searchFor(){
			this.page = 1
			this.haveMore = true
			this.pull()
		},
		usePre(i){
// 			let arr = {
// 				DataSource:1,
// 				DataId:i.Id
// 			}
// 			this.$store.dispatch('pushdataSource',arr)
			this.$store.dispatch('pushchList', [])
			this.$store.dispatch('pushisUsePre',false)
			this.$router.push('/cM/prescription?DataSource=1&DataId='+i.Id) 
		},
		go(i){
			this.$router.push('/prescriptionDetail?dId='+i.Id) 
		},
        pull(h){
            this.$post('Prescription/Prescription/GetTCMPrescriptionClassics',{
            	Name:this.search,
            	ClassId:this.ClassId,
            	PageIndex:this.page,
            	PageSize:15,
            }).then(o => {
            	this.pageShow = true
            	if (!o.ReturnData || o.ReturnData.length < 15){
            		this.haveMore = false
            	}
            	else {
            		this.haveMore = true
            	}
            	if (h){
            	    o.ReturnData.forEach(v => {
            	        this.list.push(v)
            	    })
            	}
            	else {
            	    this.list = o.ReturnData
            	}
            })
        },
         getMore(){
            const el = this.$refs.el
            let timer = null
            window.onscroll = () => {
                clearTimeout(timer)
                timer = setTimeout(() => {
                    const top = document.documentElement.scrollTop
                                || window.pageYOffset
                                || document.body.scrollTop,
                            h = el.scrollHeight,
                            wh = window.innerHeight
                    if (h - top - wh < 100 && this.haveMore){
                        this.page += 1
                        this.pull(true)
                    }
                }, 100)
            }
        },
    }
}



